<template>
    <div ref="trys" style="height: calc(100% - 0em)">
      <div v-if="showInfo" class="info-container">
        <listInfo :row="row" @closeDiv="closeDiv" ref="info" :controls="controls" :deviceType="deviceType"></listInfo>
      </div>
      <div v-if="showAdd" class="info-container " >
        <listAdd :deviceType="deviceType" @closeDiv="closeDiv" ref="add"></listAdd>
      </div>
        <div v-show="!showAdd && !showInfo" class="all-container">
          <div class="operate-div" ref="operateDiv">
            <template v-for="(item, $index) in FormOpt">
              <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options" @getInput="getFrom"></inputs>
              <selects ref="selects" v-if="item.type == 'select'" :option="item.options"
                       @getSelect="getOption"></selects>
              <datePicker ref="timer" v-if="item.type == 'timer'" type="doubleDate" :dateOpt="timeOpt" @getDatePicker="getOption" :showAfterDisabled = "showDisable"></datePicker>
            </template>
          </div>
          <div class="operate-div operateBtn">
            <!--v-if="controls[5]" v-if="controls[7]" -->
            <button  class="button-operate" @click="addList" v-if="controls[1]">
              <i class="hasIcon hasImg">
               <img src="dist/img/configuration/file-icon.png">
              </i>
            </button>
          </div>
         <!--  <div class="deepFilter" ref="deepFilter">
            <label></label>
            <template v-for="(data, $index) in deepSearchData" v-if="data.label !== ''">
              <span class="deepSearchSpan" :style="{'padding-right': data.flag ?  '1.5em' : '0.5em'}" >{{data.label}} {{data.value}}
                <i v-if="data.flag"  @click=" removeSearchData($index)">
                  <icon name="remove" scale="1"></icon>
                </i>
              </span>
            </template>
          </div> -->
          <div ref="myOwnTable" class="table-container " :style="{height: tableHeight || 'calc(100% - 4.5em)'}">
            <tables ref="myTable" v-if="showTable" :tableOpt="TableOpt" class="tablePadding"
                    @rowDbClick="clickTableRow"></tables>
          </div>
        </div>
    </div>
</template>
<script>
    import listInfo from './Info.vue'
    import listAdd from './Add.vue'
    export default {
        data () {
            return {
                showDisable: false,
                tableHeight: '',
                showTable: false, //table
                row: '',//table
                showAdd: false, //
                showInfo: false,//
                controls: [], //
                searchType: 'taskContent',
                listDate: '', //
                searchParam: {
                   deviceType: '',
                   startTime: '',
                   endTime: '',
                   taskFlag: ''
                },
                TableOpt: {
                    column: [
                        {
                            title: '',
                            field: 'taskContent',
                            textAlign: 'center',
                            width: '200'
                        },
                        {title: '', field: 'meterCount', textAlign: 'center', width: '80', sortable: false},
                        {title: '', field: 'complateCount', textAlign: 'center', width: '80', sortable: false},
                        {title: '', field: 'queryCount', textAlign: 'center', width: '80', sortable: false},
                        {title: '', field: 'createTime', textAlign: 'center', width: '140', sortable: false},
                        {title: '', field: 'endTime', textAlign: 'center', width: '140', sortable: false},
                        {title: '', field: 'updateTime', textAlign: 'center', width: '140', sortable: false},
                        {title: '', field: 'userName', textAlign: 'center', width: '140', sortable: false}
                    ],
                    url: 'replenish/replenishWorkOrderList',
                    method: 'post',
                    pageSize: 20,
                    pageNumber: 1,
                    pageList: [20, 40, 60],
                    sortName: 'updateTime',
                    sortOrder: 'desc',
                    queryParam: {deviceType: '07'}
                },
                deepSearchData: []
            }
        },
        computed: {
          FormOpt: function () {
              return  [
                {
                  type: 'select',
                  options : {
                    width: '7em',
                    cs: 'client',
                    data: this.getEnumItem('deviceType').obj,
                    text: 'text',
                    value: 'value',
                    key: 'searchParam.deviceType'
                  }
                },
                {
                  type: 'select',
                  options : {
                    width: '7em',
                    cs: 'client',
                    data: this.getEnumItem('taskFlag').obj,
                    text: 'text',
                    value: 'value',
                    key: 'searchParam.taskFlag'
                  }
                },
                {
                  type: 'timer'
                },
                {
                  type: 'input',
                  options: {
                    type: 'typeSearch',
                    key: 'searchParam',
                    selectOpt: {
                      width: '7em',
                      cs: 'client',
                      data: [
                        {text: '', value: 'taskContent'}
                      ],
                      text: 'text',
                      value: 'value',
                      key: 'searchType'
                    }
                  }
                }
              ]
            },
          deviceType: function () {
            return this.searchParam.deviceType
          },
          timeOpt: function () {
            let dates = this.initDate(true);
            return {
              key: 'listDate',
              width: '20em',
              value: {startDate: dates.start, endDate: dates.end}
            }
          }
        },
        components: {
            listInfo,
            listAdd
        },
        methods: {
            //
            deleteList: function () {
                let that = this;
                this.getConfirm('', '',{
                      confirm: function() {
                          that.getHttpRequest({
                              url: '/remSystem/',
                              param: ''
                          })
                      }
                    })
            },
            getFrom: function (val) {
            if (val.combine) {
              if (this.searchParam[val.key.split('.')[1]] !== val.val) {
                this.searchParam = this.cleanSearchData(this.searchParam, ['deviceType', 'startTime', 'endTime']);
                this.getOption(val, true);
                this.getParams(1);
              }
            } else {
              this.getOption(val, true);
            }
          },
            resetDeepData: function(val) {
              let that = this;
              let data = that.initDate(true);
              that.searchParam.startTime = data.start;
              that.searchParam.endTime = data.end;
              that.deepSearchData = [];
              that.deepSearchData.push({label: '', value: this.getEnumItem('deviceType', val).text, key: 'deviceType', sign: false});
              that.deepSearchData.push({label: '', value: data.start, key: 'startTime', sign: false});
              that.deepSearchData.push({label: '', value: data.end, key: 'endTime', sign: false});
            },
            removeSearchData: function (index) {
              let key = this.deepSearchData[index].key;
              if (key === 'taskFlag') {
                this.$refs['selects'][1].constant = {text: '', value: ''};
                this.searchParam.taskFlag = '';
              } else if (key === this.searchType) {
                this.searchParam[key] = this.$refs['inputs'][0].search = '';
              }
              this.getParams(1);
            },
            //
            closeDiv: function (val) {
                this.showAdd = false;
                this.showInfo = false;
                if (val) {
                    this.$refs.inputs[0].search = this.searchData = '';
                    this.getParams(1);
                } else {
                    this.getParams();
                }
            },
           //
            addList: function () {
                this.showAdd = true;
            },
           
            //
            getParams: function (pageNumber) {
                this.deepSearchData = [];
                let arr = this.getSearchData(this.searchParam);
                let labelName = '';
                for (let key in arr) {
                  let value = arr[key];
                  let removeFlag = true;
                  switch (key) {
                    case 'taskContent':
                      labelName = '';
                      break;
                    case 'deviceType':
                      labelName = '';
                      value = this.getEnumItem('deviceType', value).text;
                      removeFlag =  false;
                      break;
                    case 'startTime':
                      labelName = '';
                      removeFlag =  false;
                      break;
                    case 'endTime':
                      labelName = '';
                      removeFlag =  false;
                      break;
                    case 'taskFlag':
                      labelName = '';
                      value = this.getEnumItem('taskFlag', value).text;
                      break;
                  }
                  this.deepSearchData.push({label: labelName, value: value, flag: removeFlag, key: key})
                }
              this.getHeight();
              this.searchTable(arr, pageNumber);
            },
            //table
            clickTableRow: function (val) {
                this.row = val.val;
                this.showInfo = true;
            },
            init: function () {
                let data = this.initDate(true);
                this.TableOpt.queryParam = {deviceType: '07', startTime: data.start, endTime: data.end};
                this.showTable = true;
            },
            windowChange: function () {
              let that = this;
              if (that.showInfo) {
                that.$refs.info.$refs.myTable[0].onResize(false, that.$refs.info.$refs.myOwnTable[0])
              } else if (that.showAdd) {
                that.$refs.add.$refs.myTable.resizeTable(false, that.$refs.add.$refs.myOwnTable.clientHeight - 28)
              } else {
                that.getHeight()
              }
            /*  window.onresize = function () {
                if (that.showInfo) {
                  that.$refs.info.$refs.myTable[0].onResize(false, that.$refs.info.$refs.myOwnTable[0])
                } else if (that.showAdd) {
                  that.$refs.add.$refs.myTable.resizeTable(false, that.$refs.add.$refs.myOwnTable.clientHeight - 28)
                } else {
                  that.getHeight()
                }
              }*/
            }
        },
        mounted: function () {
            let that = this;
           /* window.onresize = function () {
              that.windowChange();
            }*/
           // that.isIn();
           // that.getAction();
        },
        created: function () {
            this.isIn();
            this.getAction();
        },
      watch: {
        'searchParam.deviceType': {
          handler(val, oldVal) {
            if (val !== oldVal) {
              let dates = this.initDate(true);
              this.$refs['inputs'][0].search = '';
              this.$refs['selects'][1].constant = {text: '', value: ''};
              this.searchParam.taskFlag = '';
              this.taskTag = false;
              this.searchParam = this.cleanSearchData(this.searchParam, ['deviceType', 'startTime', 'endTime']);
              if (dates.start !== this.searchParam['startTime'] || dates.end !== this.searchParam['endTime'] ) {
                this.$refs.timer[0].dateOpt.value = {startDate: dates.start, endDate: dates.end};
                this.$refs.timer[0].init();
                this.resetDeepData(val);
                this.measureDate = dates.start + '' + dates.end;
              } else {
                this.getParams(1)
              }
            }
          }
        },
        'searchParam.taskFlag': {
          handler(val) {
              this.getParams(1)
            }
        },
        'listDate': {
          handler(val, oldVal) {
            if (val !== oldVal && val !== '') {
              let arr = val.split('');
              this.searchParam.startTime = arr[0];
              this.searchParam.endTime = arr[1];
              this.getParams(1)
            }
          }
        }
      }
    }
</script>

<style lang="scss">

    button.buttonComL {
        margin: 0;
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
    }

    button.buttonComR {
        margin: 0 0 0 -3px;
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
    }

    button.buttonComS {
        color: white;
        background: #0A9DE2;
    }

    .formBackIndex {
        z-index: 19
    }

    .formIndex {
        z-index: 20
    }


</style>



