<template>
    <div class="info-div" style="height:100%">
      <div class="info-header">
        <span class="callback" @click="resetComponent"> </span>
        <span> > </span>
      </div>
        <VuePerfectScrollbar class="info-container" style="margin-top: 1em;height: calc(100% - 1em);" >
            <div class="list-content detail-content list-contentB" style="height: 12em" >
              <div class="list-header" >
                <i class="hasIcon hasImg"><img src="dist/img/measure/base-inf-icon.png" ></i>
                
                <!--<button class="list-operate" @click=""><i class="hasIcon"><icon name="save" ></icon></i></button>-->
              </div>
                <ul class="form-list" ref="formList" style="padding-top:0.8em">
                    <li  v-for="(items, $indexs) in fromList" :class="items.classes" style="margin:0.2em 1em!important;width:21em">
                       <span  v-if="!items.isEmpty" class="label-show " >
                          <span class="emptyTips">* </span>
                          {{items.label}}&nbsp; </span>
                      <span  v-if="items.isEmpty" class="label-show " >
                          <span >&nbsp;</span>
                          {{items.label}}&nbsp; </span>
                      <inputs :ref="items.option.key" v-if="items.type == 'input'" @getInput="getFrom" :option="items.option"></inputs>
                      <div style="margin:2.1em 0 0 1em;float:left;font-size:0.875em" v-if="items.type == 'input'" v-html="getLastLength"></div>
                      <datePicker  :ref="items.option.key" v-if="items.type == 'timer'" @getDatePicker="getFrom" :dateOpt="items.option" :showBeforeDisabled="true"></datePicker>
                      <combine  :ref="items.option.key" v-if="items.type == 'combineSelect'" @getSelects="getOperator" :option="operators" width="16em"></combine>
                    </li>
                </ul>
            </div>
            <div class="list-content detail-content list-contentB"  style="height: calc(100% - 20em);" >
              <div class="list-header" style="margin-bottom:0">
                <i class="hasIcon hasImg"><img src="dist/img/measure/mechine-icon.png" ></i>
                
              </div>
                    <div class="operate-div" style="float:left;padding:0.8em 0">
                        <combine ref="getDevice" :option="combineOption" width="30em" @getSelects="selectList" :queryParam="{deviceType: deviceType}"></combine>
                        <selects  style="margin-left:10px" :option="selectOption"
                                  @getSelect="selectClick"></selects>
                        <selects  style="margin-left:40px;" :option="deleteOption"
                                  @getSelect="removeList"></selects>
                        <!--buttonStyle-->
                        <!--<importexcel></importexcel>-->
                        <!--<button  type="button" class="operateShow  buttonStyle"  @click="removeList"> <i class="glyphicon glyphicon-trash"></i> </button>-->
                    </div>
                   <div ref="myOwnTable" class="table-container" style="padding:0;height:calc(100% - 7.5em);width:100%" >
                     <tables  :tableOpt="TableOpt" ref="myTable" v-if="showTable" class="smallHeight"></tables>
                   </div>
            </div>
            <div  class="makeSure" style="font-size:0.94em">
                <button class="button-default" @click="listSubmit"></button>
                <button class="button-default cancel-button" @click="resetComponent"></button>
            </div>
        </VuePerfectScrollbar>
      <dialogs type="confirm" ref="confirms"> </dialogs>
      <dialogs type="alert" ref="alerts"> </dialogs>
      <dialogs type="loading" ref="loadings"> </dialogs>
    </div>
</template>
<script>
    import combine from  '../../../component/childComponent/CombineSelect.vue'
    export default{
        data() {
           return {
               showTable: false,
               runtorError: false,
               searchType: 'customerContact',
               selectLists: '',
               deleteType: '',
               selectData: '',
               list: {
                   runtor: '',
                   taskContent: '',
                   endTime: ''
               },
               fromList: [
                 {
                   type: 'combineSelect',
                   isEmpty: false,
                   label: '',
                   option: {
                     type: 'normalInput',
                     width: '12em',
                     key: 'list.runtor',
                     placeHolder: '20',
                     length: 20,
                     errorMsg: '20'
                   }
                 },
                 {type: 'timer',  classes: 'hasMarginLi', isEmpty: true, label: '', option: {key: 'list.endTime', width: '16em'}},
                 {
                   type: 'input',
                   classes: 'allWidth',
                   isEmpty: false,
                   label: '',
                   option: {
                     type: 'textArea',
                     width: '48em',
                     key: 'list.taskContent',
                     placeHolder: '200',
                     regular: 'remark',
                     length: 200,
                     errorMsg: '200'
                   }
                 },
               ],
               TableOpt: {
                   column: [
                       {
                           title: '',
                           field: 'customerName',
                           textAlign: 'center',
                           width: '80'
                       },
                       {
                           title: '',
                           field: 'propertyName',
                           textAlign: 'center',
                           width: '140'
                       },
                       {
                           title: '',
                           field: 'customerAddr',
                           textAlign: 'center',
                           width: '140'
                       },
                       {
                           title: '',
                           field: 'meterUserNo',
                           textAlign: 'center',
                           width: '120'
                       },
                       {
                           title: '',
                           field: 'deviceNo',
                           textAlign: 'center',
                           width: '120'
                       },
                       {
                           title: '',
                           field: '',
                           textAlign: 'center',
                           width: '100',
                           type: 'object',
                           formatter: function (val, index, row, obj) {
                               return {
                                   template: '<button class="list-operate" @click="removeList"> </button>',
                                    methods: {
                                       removeList: function () {
                                           let arr = [];
                                           arr.push(row);
                                          obj.$parent.$parent.operateList(1, arr);
                                       }
                                   }
                               }
                           }
                       }
                   ],
                   pageSize: 15,
                   pageNumber: 1,
                   sortName: '',
                   sortOrder: 'asc',
                   height: 200,
                   data: [],
                   rowName: 'data'
                   //  isExport: true,
                   //   exportURL: '/rmcSystem/customerRecord/generateExcel'

               },
               combineOption: {
                   inputOpt: {
                       width: '30em',
                       type: 'typeSearch',
                     
                       inputIcon: 'plus',
                       //   clear: true, //
                       selectOpt: {
                           width: '7em',
                           cs: 'client',
                           data: [
                               {text: '', value: 'customerAddr'},
                               {text: '', value: 'customerName'},
                               {text: '', value: 'meterUserNo'},
                               {text: '', value: 'deviceNo'}
                           ],
                           key: 'searchType'
                       }
                   },
//                  method: 'get',
//                   url: 'dist/json/allData.json',
                  method: 'post',
                   url: 'replenish/queryReplenishMeter'
               },
               operators: {
                   inputOpt: {
                       width: '16em',
                       type: 'typeSearch',
                       hideSelect: true,
                       //   clear: true, //
                       selectOpt: {
                           width: '7em',
                           cs: 'client',
                           data: [
                               {text: '', value: 'userName'}
                           ],
                           key: 'searchType'
                       }
                   },
                   autoSearch: true,
                   url: 'replenish/queryUser',
                   paramType: 'form',
                   method: 'post'
//                   method: 'get',
//                   url: 'dist/json/allUsers.json'
               },
               deleteOption: {
                   key: 'deleteType',
                   cs: 'client',
                   width: '11em',
                   constant: {text: '', value: ''},
                   data: [
                       {text: '', value: 0},
                       {text: '', value: 1}
                   ],
                   text: 'text',
                   value: 'value',
                   rowName: ''
               }
           }
        },
        props: ['deviceType'],
        methods: {
          getFrom: function (val) {
             this.getOption(val, this);
          },
            removeList: function (value) {
                let val = value.val;
                let pageNumber;
                if(val === 1) {
                    pageNumber = 'all'
                }
                if (this.$refs.myTable) {
                   this.$refs.myTable.clearAllData(pageNumber);
                }
            },
            getOperator: function (val) {
               
                if (val.userId !== null && val.userId !== '') {
                    this.list['runtor'] = val.userId;
                    this.runtorError = false;
                }

                if(val === '') {
                   this.list['runtor'] = '';
                   this.runtorError = false
                } else if(val === 'error') {
                   this.list['runtor'] = '';
                   this.runtorError = true;
                }
            },
            resetComponent: function () {
                let that = this;
                that.$emit('closeDiv');
            },
            cancelAdd: function () {

            },
            listSubmit: function () {
                let that = this;
                let params = {
                    taskContent: that.list.taskContent,
                    endTime: that.list.endTime,
                    deviceType: that.deviceType,
                     runtor: that.list.runtor
                };
                let meter = that.$refs.myTable.getAllData();
                let arr = [];
                let length = meter.length;
                if(that.list.runtor !== '') {
                 
                    if(that.list.taskContent !== '' && typeof that.list.taskContent !== 'undefined') {
                       if (length > 0) {
                        for(let i=0; i<length; i++) {
                            arr.push({cno: meter[i].cno, customerNo: meter[i].customerNo})
                        }
                        params['meters'] = arr;
                        that.conforms.show({
                            msg: '',
                            action: {
                              confirm: function () {
                                that.loadings.show({
                                  msg: ''
                                });
                                that.getRequest({
                                  url: 'replenish/createReplenishWorkOrder',
                                  param: params,
                                  method: 'post',
                                  success: function (data) {
                                    that.loadings.close();
                                    that.alerts.show({
                                      msg: data.message
                                    });
                                    setTimeout(function () {
                                      that.resetComponent();
                                    }, 500)

                                  },
                                  error: function (data) {
                                    that.loadings.close();
                                    that.alerts.show({
                                      msg: data.message
                                    })
                                  }
                                });
                              }
                            }
                        })
                    } else {
                        that.alerts.show({
                            msg: ''
                        });
                    }
                  } else {
                     that.alerts.show({
                            msg: ''
                        });
                  }
                   
                } else {
				    let msg = '';
				    if(that.list.runtor == '' && !that.runtorError) {
					      msg = '';
				    	}
                    that.alerts.show({
                        msg: msg
                    });
                }
            },
            //type: 0, 1: 
            operateList: function (type, data) {
                if (this.$refs.myTable) {
                    if (data !== null && typeof data !== 'undefined') {
                        this.$refs.myTable.operateData(data, ['customerNo', 'cno'], type);
                    }
                }
            },
            selectList: function (data) {
                if (data.value !== null && data.value !== '' && data !== '') {
                    this.selectData = data.value;
                    let arr = [];
                    arr.push(this.selectData);
                    this.operateList(0, arr);
                    this.$refs.getDevice.$refs.input.search = '';
                }
            },
            selectClick: function (data) {
                let that = this;
                let val = data.val;
                if (val!== '') {
                    this.loadings.show({
                        titleMsg: '',
                        msg: ''
                    });
                    this.selectLists = val;
                    this.getRequest({
                       url:'freezeAnalyze/readCollectRecordDetail',
                       method: 'post',
                        // url: 'dist/json/addMeasure.json',
                        // method: 'get',
                        param:{flag: 0 , date: this.selectLists, deviceType: that.deviceType, pageSize: 1000000, pageNumber: 1, sortName: 'failCount'},
                        success: function (data) {
                          that.loadings.close();
                          if (data.data.length === 0) {
                            that.alerts.show({
                              msg: ''
                            })
                          } else {
                            that.conforms.show({
                              msg: '',
                              action:{
                                 confirm: function () {
                                   that.operateList(0, data.data)
                                  }
                               }
                            })
                          }
                        },
                       error: function (data) {
                         that.alerts.show({
                           msg: data.message
                         })
                       }
                    })
                }
            }
        },
        components: {
            combine
        },
        computed: {
            getLastLength: function () {
                let len = 0;
                if(this.list.taskContent) {
                  len = this.list.taskContent.length
                }
                return ' <span style="color:#F2595B">' + (200 - len) + '</span>';
            },
            titleInfo: function () {
                let typeName;
                switch(this.deviceType) {
                    case '07':
                        typeName = '';
                        break;
                    case '08':
                        typeName = '';
                        break;
                    case '09':
                        typeName = '';
                        break
                }
                return  '<span>' + typeName +'</span> ';
            },
            conforms: function () {
                return this.$refs.confirms
            },
            alerts: function () {
                return this.$refs.alerts
            },
            loadings: function () {
               return this.$refs.loadings
            },
            selectOption: function (){
                let date = new Date(new Date()-24*60*60*1000);
                let month = date.getMonth() + 1;
                let day = date.getDate() ;
                let theDate = date.getFullYear() + '-' + (month > 9 ? month : '0' + month ) + '-' + (day > 9 ? day : '0' + day);

                return {
                    key: 'selectLists',
                    cs: 'client',
                    width: '13em',
                    constant: {text: '', value: ''},
                    data: [
                        {text: theDate + ' ', value: theDate}
                    ],
                    rowName: ''
                }
            }
        },
        mounted: function (){
          let that = this;
          that.$nextTick(function () {
              that.TableOpt.height = that.$refs.myOwnTable.clientHeight - 30;
              that.showTable = true
          })
        
        },
        watch: {
        }
    }
</script>
<style lang="scss" scope>
    .tabSelected{
        background :#0CC6AF!important;
        color:white!important;
        outline: none;
    }
    .doubleLi{
        width: 350px!important;
    }
    .maxLi {
        width: 100%!important;
    }
    .buttonStyle {
        height:35px;
        float:left;
        margin-left:10px;
     //   background:white!important;
    //    color:black!important;
    }
    .smallHeight{
       .table{
        td{
          padding: 5px!important;
        }
        thead{
          tr{
            th{
              padding:12px 5px!important;
            }
          }
        }
       }
    }
</style>



