<template>
    <div class="info-div">
        <div class="info-header">
          <span class="callback" @click="resetComponent"> </span>
          <span> > </span>
        </div>
        <VuePerfectScrollbar class="info-container" v-if="show" ref="infoContainer" >
            <div v-for="(item, $index) in formOpt" class="list-content list-contentB" style="padding: 1em 2em" :style="{height:item.height }">
                <div class="list-header" >
                    <i class="hasIcon hasImg headerImg">
                      <img :src="item.img" />
                    </i>
                    {{item.name}}
                </div>
                <template v-if="item.listType === 'table'" >
                  <div class="tabs-show tab-div" style="margin:0.4em 0 1em 0!important">
                    <ul>
                      <li v-for="(tabs, $index) in btnOpt" :class="{'selectedLi':  $index == tabIndex}" style="font-size:0.92em"
                          @click="getStatusData(tabs.status,$index)">
                        <template v-if="tabs.showNum">
                          {{tabs.name}} ( {{tabs.num}} )
                        </template>
                        <template v-else>
                          {{tabs.name}}
                        </template>
                      </li>
                    </ul>
                  </div>
                  <div ref="myOwnTable" class="table-container dialogTable" style="padding:0;height:calc(100% - 7.5em);width:100%;font-size:0.94em" >
                    <tables ref="myTable" class="" v-if="showTable" :tableOpt="tableInfo" @getOtherData="getTotals"></tables>
                  </div>
                </template>
                <ul v-if="item.listType === 'list'" class="form-list" ref="listContainer" >
                    <li v-for="(items, $indexs) in lists" style="margin: 0 1em">
                      <span class="label-span">{{items.name}} : &nbsp;&nbsp; </span>
                      <span class="label-span" style="text-align: left">{{items.value}}</span>
                    </li>
                </ul>
            </div>
        </VuePerfectScrollbar>
    </div>
</template>
<script>
    import openCard from '../../childComponent/OpenAccount.vue'
    export default{
      data() {
        return {
          btnOpt: [
            {name: '', num: 0, status: 1, flag: 0, showNum: true,   total: 'successCount'},
            {name: '', num: 0, status: 1, flag: 1, showNum: true, total: 'failCount'},
            {name: '', num: 0, status: 0,flag: 2, showNum: true, total: 'unfinishCount'},
            {name: '',  status: 2,showNum: false}
          ],
          tabIndex: 0,
          tableInfo: {
//            method: 'get',
//            url: 'dist/json/measureAgainSuccess.json',
            method: 'post',
            url: 'replenish/replenishDataDetail',
            column: [
              {
                title: '',
                field: 'customerName',
                textAlign: 'center',
                width: '80'
              },
              {
                title: '',
                field: 'meterUserNo',
                textAlign: 'center',
                width: '120'
              },
              {
                title: '',
                field: 'cno',
                textAlign: 'center',
                width: '140',
                sortable: false,
                type: 'string',
                formatter: function (val, index, row) {
                  if (val !== null && val !== '') {
                    return val.substr(2).replace(/^0*/gi, '')
                  }
                }
              },
              {
                title: '',
                field: 'dataSrc',
                textAlign: 'center',
                width: '90',
                sortable: false,
                type: 'string',
                formatter: function (val, index, row) {
                  if (val !== null && val !== '') {
                    switch(val) {
                      case 1:
                        return '';
                      case 2:
                        return '';
                      case 3:
                        return ''  
                    }
                  }
                }
              },
              {title: '', field: 'pr0', textAlign: 'center', width: '100', sortable: false},
              {title: '', field: 'balance', textAlign: 'center', width: '80', sortable: false},
              {title: '', field: 'collectTime', textAlign: 'center', width: '140', sortable: false},
              {title: '', field: 'customerAddr', textAlign: 'center', width: '150', sortable: false},
              {title: '', field: 'collectUserName', textAlign: 'center', width: '80', sortable: false}
            ],
            pageSize: 20,
            totalName: 'successCount',
            pageNumber: 1,
            pageList: [20, 40, 60],
            queryParam: {taskNo: '', status: 1, errCode: 0},
            rowName: 'data'
          },
          showTable: false,
          show: true,
          sign: -1, //1: 2
          dataReady: false,
          listInfoEdit: false,
          formOpt: [
            {
              name: '',
              img: 'dist/img/realTime/Work-order-icon.png',
              listType: 'list',
              isShow: true,
              height: '8.5em'
            },
            {
              name: '',
              img: 'dist/img/realTime/work-oder-finished.png',
              listType: 'table',
              isShow: false,
              height: 'calc(100% - 14em)'
            }
          ]
        }
      },
      props: ['row', 'controls', 'deviceType'],
      components: {openCard},
      computed: {
        titleInfo: function () {
          return '';
        },
       
        lists: function () {
            return [
                {name: '', value: this.row.userName},
                {name: '', value: this.row.createTime},
                {name: '', value: this.row.taskContent},
                {name: '', value: this.row.meterCount},
                {name: '', value: this.row.endTime || '--'}
              ]
        }
      },
      methods: {
        getTotals: function (val) {
          this.btnOpt[0].num = val.successCount;
          this.btnOpt[1].num = val.failCount;
          this.btnOpt[2].num = val.unfinishCount;
        },
        getStatusData: function (status, index) {
          let columns = [
            [
              {
                title: '',
                field: 'customerName',
                textAlign: 'center',
                width: '80'
              },
              {
                title: '',
                field: 'meterUserNo',
                textAlign: 'center',
                width: '120'
              },
              {
                title: '',
                field: 'cno',
                textAlign: 'center',
                width: '140',
                sortable: false,
                type: 'string',
                formatter: function (val, index, row) {
                  if (val !== null && val !== '') {
                    return val.substr(2).replace(/^0*/gi, '')
                  }
                }
              },
              {
                title: '',
                field: 'dataSrc',
                textAlign: 'center',
                width: '90',
                sortable: false,
                type: 'string',
                formatter: function (val, index, row) {
                  if (val !== null && val !== '') {
                     switch(val) {
                      case 1:
                        return '';
                      case 2:
                        return '';
                      case 3:
                        return ''  
                    }
                  }
                }
              },
              {title: '', field: 'pr0', textAlign: 'center', width: '100', sortable: false},
              {title: '', field: 'balance', textAlign: 'center', width: '80', sortable: false},
              {title: '', field: 'collectTime', textAlign: 'center', width: '140', sortable: false},
              {title: '', field: 'customerAddr', textAlign: 'center', width: '150', sortable: false},
              {title: '', field: 'collectUserName', textAlign: 'center', width: '80', sortable: false}
            ],
            [
              {
                title: '',
                field: 'customerName',
                textAlign: 'center',
                width: '80'
              },
              {
                title: '',
                field: 'meterUserNo',
                textAlign: 'center',
                width: '120'
              },
              {
                title: '',
                field: 'cno',
                textAlign: 'center',
                width: '140',
                sortable: false,
                type: 'string',
                formatter: function (val, index, row) {
                  if (val !== null && val !== '') {
                    return val.substr(2).replace(/^0*/gi, '')
                  }
                }
              },
              {
                title: '',
                field: 'dataSrc',
                textAlign: 'center',
                width: '90',
                sortable: false,
                type: 'string',
                formatter: function (val, index, row) {
                  if (val !== null && val !== '') {
                     switch(val) {
                      case 1:
                        return '';
                      case 2:
                        return '';
                      case 3:
                        return ''  
                    }
                  }
                }
              },
              {title: '', field: 'errInfo', textAlign: 'center', width: '140', sortable: false},
              {title: '', field: 'customerAddr', textAlign: 'center', width: '150', sortable: false},
              {title: '', field: 'collectUserName', textAlign: 'center', width: '80', sortable: false}
            ],
            [
              {
                title: '',
                field: 'customerName',
                textAlign: 'center',
                width: '80'
              },
              {
                title: '',
                field: 'meterUserNo',
                textAlign: 'center',
                width: '120'
              },
              {
                title: '',
                field: 'cno',
                textAlign: 'center',
                width: '140',
                sortable: false,
                type: 'string',
                formatter: function (val, index, row) {
                  if (val !== null && val !== '') {
                    return val.substr(2).replace(/^0*/gi, '')
                  }
                }
              },
              {title: '', field: 'customerAddr', textAlign: 'center', width: '150', sortable: false}
            ],
            [
              {title: '', field: 'queryUserName', textAlign: 'center', width: '100', sortable: false},
              {
                title: '',
                field: 'orgName',
                textAlign: 'center',
                width: '120'
              },
              {
                title: '',
                field: 'userMobile',
                textAlign: 'center',
                width: '120'
              },
              {title: '', field: 'queryTime', textAlign: 'center', width: '120', sortable: false}
            ]
          ];
          let that = this;
          that.tabIndex = index;
          let param = {status: status, taskNo: that.row.taskNo};
          if (index === 3) {
            param = {taskNo: that.row.taskNo};
            that.getRequest({
              url: 'replenish/requestWorkOrderList',
              param: param,
              method: 'post',
              success: function (data) {
                that.$refs.myTable[0].resetTableOpt({
                  url: '',
                  data:  data.data || [],
                  column: columns[index]
                })
              }
            }, true)
          } else {
           param['errCode'] = this.btnOpt[index].flag;
            that.$refs.myTable[0].resetTableOpt({
              url: 'replenish/replenishDataDetail',
              totalName: that.btnOpt[index].total,
              column: columns[index],
              queryParam: param
            })
          }

        },
        getAccountInfo: function (val) {
          this.getDeviceInfo(val)
        },
        resetComponent: function () {
          let that = this;
          that.show = false;
          that.$emit('closeDiv');
          setTimeout(function () {
            that.show = false;
          }, 0)
        },
        init: function () {
          let that = this;
          that.tableInfo.queryParam = {taskNo: that.row.taskNo, status: 1, errCode: 0};
          that.showTable = true;
          this.$nextTick(function () {
            that.$refs.myTable[0].onResize(false, that.$refs.myOwnTable[0]);
          })
        }
      },
      mounted: function () {
        this.isIn();
        this.init();
        //   this.getAction();
      },
      watch: {
      }
    }
</script>
<style lang="scss">
    @import "../../../assets/main.scss";
    a {
        margin-right: 10px
    }
    .info-container {
        margin-top: 5px
    }
    .tabSelected {
      background: #0CC6AF !important;
      color: white !important;
      outline: none;
    }
    .label-span {
      @extend .textOverflow
    }
    .small-margin {
        padding: 5px 0 !important;
        width: 250px !important;
       .text-span {
       width: calc(290px - 140px - 10px) !important;
       }
    }

    .list-content {
      .form-list {
        span.leftAgain {
          text-align: left !important;
          font-weight: 600;
        }
        ul.ul-padding {
          margin-top: 5px;
          padding: 0 0 0 30px;
        }
      }
    }
</style>



